// stylelint-disable

@mixin pseudo($position: absolute, $display: block, $content: '',$extend: true) {
  @if $position == 'absolute' and $display == 'block' and $content == '' and $extend == true {
    position: absolute;
    content: '';
    display: block;
  } @else {
    @if $display == 'inline-block' {
      display: inline-block;
    } @else {
      display: $display;
    }
    content: '' + $content;
    position: $position;
  }
}

@mixin media-breakpoint-up($device: desktop) {
  @media #{map-get($mediaQueriesUp, $device)} {
    @content;
  }
}

@mixin media-breakpoint-down($device: desktop) {
  @media #{map-get($mediaQueriesDown, $device)} {
    @content;
  }
}


// stylelint-enable
