.form {
  display: flex;
  flex-direction: column;
}

.form-field {
  width: 100%;

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.form-field-label {
  display: block;
  width: 100%;
  height: 48px;
}

.form-field-input {
  border: 0;
  display: block;
  width: 100%;
  height: 100%;
  padding: 0 16px;
  border-radius: 8px;
  border: 1px solid $gray-300;
  overflow: hidden;

  %page-dark & {
    background-color: transparent;
    border: 1px solid rgba(255, 255, 255, 0.15);
    color: rgba($color: $white, $alpha: 0.35);
  }

  &:hover {
    border-color: $gray;
  }

  &:focus,
  &:focus-visible {
    border-color: $gray;
    box-shadow: none;
    outline: none;
  }
}

.form-policy {
  color: $gray;
  font-size: 14px;
  line-height: 1.5em;

  a {
    color: $gray;
    text-decoration: underline;
  }
}